"use client";
import { useState } from "react";

interface Opportunity {
  id: number;
  title: string;
  professor: string;
  institute: string;
  field: string;
  duration: string;
  prerequisites: string;
}

export default function ProfessorDashboard() {
  const [opportunities, setOpportunities] = useState<Opportunity[]>([
    {
      id: 1,
      title: "Internship",
      professor: "Dr. A",
      institute: "BITS Pilani",
      field: "Data Structures",
      duration: "6 months",
      prerequisites: "C++, Algorithms",
    },
  ]);

  const [showEditModal, setShowEditModal] = useState(false);
  const [showPostModal, setShowPostModal] = useState(false);
  const [selectedTitle, setSelectedTitle] = useState("");
  const [newOpp, setNewOpp] = useState({
    title: "",
    professor: "",
    institute: "",
    field: "",
    duration: "",
    prerequisites: "",
  });

  const handleEdit = (title: string) => {
    setSelectedTitle(title);
    setShowEditModal(true);
  };

  const handleDelete = (id: number) => {
    const confirmed = confirm("Are you sure you want to delete this opportunity?");
    if (confirmed) {
      setOpportunities(opportunities.filter((opp) => opp.id !== id));
    }
  };

  const handlePost = () => {
    if (
      !newOpp.title ||
      !newOpp.professor ||
      !newOpp.institute ||
      !newOpp.field ||
      !newOpp.duration
    ) {
      alert("Please fill all fields");
      return;
    }
    setOpportunities([
      ...opportunities,
      { id: Date.now(), ...newOpp },
    ]);
    setNewOpp({
      title: "",
      professor: "",
      institute: "",
      field: "",
      duration: "",
      prerequisites: "",
    });
    setShowPostModal(false);
  };

  return (
    <div className="bg-gray-50 min-h-screen flex flex-col">
      {/* Header */}
      <header className="bg-gradient-to-r from-blue-600 to-indigo-600 text-white p-5 shadow-md flex justify-between items-center">
        <h1 className="text-2xl font-bold">ProfConnect - Professor Dashboard</h1>
        <a
          href="/"
          className="bg-white text-blue-700 px-4 py-2 rounded-lg font-medium hover:bg-gray-100 transition"
        >
          Logout
        </a>
      </header>

      {/* Actions */}
      <section className="flex justify-end p-6">
        <button
          onClick={() => setShowPostModal(true)}
          className="bg-blue-600 hover:bg-blue-700 text-white font-semibold px-6 py-3 rounded-2xl shadow-md transition"
        >
          + Post New Opportunity
        </button>
      </section>

      {/* Filter Section */}
      <section className="bg-white shadow-md p-6 sticky top-0 z-10 flex items-center gap-4">
        <label className="font-medium text-gray-700">Filter:</label>
        <select className="border rounded-lg px-3 py-2 focus:ring focus:ring-blue-200">
          <option value="">Select</option>
          <option>Institute Name</option>
          <option>Recently Added</option>
        </select>
      </section>

      {/* Opportunities Section */}
      <main className="p-6 grid gap-6 md:grid-cols-2 lg:grid-cols-3">
        {opportunities.map((opp) => (
          <div
            key={opp.id}
            className="opportunity-card bg-white rounded-2xl shadow-md p-6 hover:shadow-lg transition flex flex-col"
          >
            <h2 className="text-xl font-bold text-indigo-700 mb-2">
              {opp.title}
            </h2>
            <p className="text-gray-600 mb-1">
              <strong>Professor:</strong> {opp.professor}
            </p>
            <p className="text-gray-600 mb-1">
              <strong>Institute:</strong> {opp.institute}
            </p>
            <p className="text-gray-600 mb-1">
              <strong>Field:</strong> {opp.field}
            </p>
            <p className="text-gray-600 mb-1">
              <strong>Duration:</strong> {opp.duration}
            </p>
            <p className="text-gray-600 mb-3">
              <strong>Pre-requisites:</strong> {opp.prerequisites}
            </p>
            <a href="#" className="text-blue-600 hover:underline text-sm mb-3">
              View PDF Details
            </a>
            <div className="flex gap-2 mt-auto">
              <button
                onClick={() => handleEdit(opp.title)}
                className="bg-yellow-500 hover:bg-yellow-600 text-white rounded-lg px-4 py-2 transition"
              >
                Edit PDF
              </button>
              <button
                onClick={() => handleDelete(opp.id)}
                className="bg-red-500 hover:bg-red-600 text-white rounded-lg px-4 py-2 transition"
              >
                Delete
              </button>
            </div>
          </div>
        ))}
      </main>

      {/* Edit Modal */}
      {showEditModal && (
        <div className="fixed inset-0 bg-black bg-opacity-40 flex justify-center items-center z-50">
          <div className="bg-white rounded-2xl shadow-lg p-8 w-full max-w-md">
            <h2 className="text-xl font-bold text-indigo-700 mb-4">
              Edit PDF for {selectedTitle}
            </h2>
            <input
              id="editPdf"
              type="file"
              className="w-full border rounded-lg p-2 mb-4"
            />
            <div className="flex justify-end gap-3">
              <button
                onClick={() => setShowEditModal(false)}
                className="bg-gray-300 hover:bg-gray-400 text-gray-800 rounded-lg px-4 py-2 transition"
              >
                Cancel
              </button>
              <button
                onClick={() => setShowEditModal(false)}
                className="bg-blue-600 hover:bg-blue-700 text-white rounded-lg px-4 py-2 transition"
              >
                Save
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Post New Opportunity Modal */}
      {showPostModal && (
        <div className="fixed inset-0 bg-black bg-opacity-40 flex justify-center items-center z-50">
          <div className="bg-white rounded-2xl shadow-lg p-8 w-full max-w-lg">
            <h2 className="text-xl font-bold text-blue-700 mb-4">
              Post New Opportunity
            </h2>

            <div className="space-y-3">
              <input
                type="text"
                placeholder="Title"
                value={newOpp.title}
                onChange={(e) =>
                  setNewOpp({ ...newOpp, title: e.target.value })
                }
                className="w-full border rounded-lg p-2"
              />
              <input
                type="text"
                placeholder="Professor Name"
                value={newOpp.professor}
                onChange={(e) =>
                  setNewOpp({ ...newOpp, professor: e.target.value })
                }
                className="w-full border rounded-lg p-2"
              />
              <input
                type="text"
                placeholder="Institute"
                value={newOpp.institute}
                onChange={(e) =>
                  setNewOpp({ ...newOpp, institute: e.target.value })
                }
                className="w-full border rounded-lg p-2"
              />
              <input
                type="text"
                placeholder="Field"
                value={newOpp.field}
                onChange={(e) =>
                  setNewOpp({ ...newOpp, field: e.target.value })
                }
                className="w-full border rounded-lg p-2"
              />
              <input
                type="text"
                placeholder="Duration"
                value={newOpp.duration}
                onChange={(e) =>
                  setNewOpp({ ...newOpp, duration: e.target.value })
                }
                className="w-full border rounded-lg p-2"
              />
              <input
                type="text"
                placeholder="Pre-requisites"
                value={newOpp.prerequisites}
                onChange={(e) =>
                  setNewOpp({ ...newOpp, prerequisites: e.target.value })
                }
                className="w-full border rounded-lg p-2"
              />
            </div>

            <div className="flex justify-end gap-3 mt-6">
              <button
                onClick={() => setShowPostModal(false)}
                className="bg-gray-300 hover:bg-gray-400 text-gray-800 rounded-lg px-4 py-2 transition"
              >
                Cancel
              </button>
              <button
                onClick={handlePost}
                className="bg-blue-600 hover:bg-blue-700 text-white rounded-lg px-4 py-2 transition"
              >
                Post
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
